import { merge } from 'lodash';

import prepareApexChartByPostType from "../../../gatsby-theme-dmglvnfinancelabs/src/utilities/prepareApexChartByPostType";

//import prepareScatterChartAbstractBetaCases from "../../../gatsby-theme-dmglvnfinancelabs/src/utilities/prepareScatterChartAbstractBetaCases"
import prepareScatterChartAbstractBetaCasesFeatured from "../../../gatsby-theme-dmglvnfinancelabs/src/utilities/prepareScatterChartAbstractBetaCases";

import features from "../../../financelabs/src/@dmglvn/gatsby-theme-dmglvnfinancelabs/myownsite/pages/betamorethanone"

export function apexchart(props) {
  //console.log(props);

  if (props.type.includes("beta")) {
    // let charts = prepareScatterChartAbstractBetaCases({ type: props.type });
    let charts = prepareScatterChartAbstractBetaCasesFeatured({
      type: props.type      
    });

   // console.log(charts);


    let labs = document.getElementById("labs");
    labs.style.padding = "2rem"

    let scatterchart = document.createElement("section");
    labs.appendChild(scatterchart);

    let chartScatter = new ApexCharts(scatterchart, prepareApexChartByPostType(merge({}, charts[0], features)));
    chartScatter.render();

    let areachart = document.createElement("section");
    labs.appendChild(areachart);
    let chartArea = new ApexCharts(areachart, prepareApexChartByPostType(charts[1]));
    chartArea.render();

    return null
  }


  var options = {
    series: [{ name: "Desktops", data: [10, 41, 35, 51, 49, 62, 69, 91, 148] }],
    chart: { type: 'line', zoom: { enabled: false }},
    dataLabels: { enabled: false},
    stroke: { curve: 'straight', },
    title: { text: 'Product Trends by Month', align: 'left' },
    grid: { row: { colors: ['#f3f3f3', 'transparent'], opacity: 0.5 }},
    xaxis: { categories: ['Jan', 'Feb', 'Mar', 'Apr', 'May', 'Jun', 'Jul', 'Aug', 'Sep']}
  };

  let chart = new ApexCharts(document.getElementById("labs"), options);

  return chart.render()

}